.service{
    background-image: url('../../assets/images/background-2.jpg');
    height: 100vh;
    width: 100%;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    display: grid;
    place-items: center;
    position: relative;
    .logo{
        position: absolute;
        top: 40px;
        left: 40px;
        z-index: 99;
    }
    h2{
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 700;
        font-size: 64px;
        line-height: 78px;
        text-align: center;
        
        color: #FAFAFA;
    }
    .card-wrapper{
        display: flex;
        justify-content: space-between;
        padding-top: 60px;
        max-width: 400px;
        margin: 0 auto;
        .card{
            box-sizing: border-box;
            background: rgba(250, 250, 250, 0.3);
            border: 1px solid #FAFAFA;
            backdrop-filter: blur(40px);
            border-radius: 30px;
            padding: 26px 30px;
            height: 500px;
            .main-title{
                background: #FAFAFA;
                border-radius: 30px;
                height: 218px;
                display: flex;
                align-items: center;
                justify-content: center;
                margin-bottom: 19px;
            }
            h3{
                font-family: 'Montserrat';
                font-style: normal;
                font-weight: 700;
                font-size: 24px;
                line-height: 29px;
                color: #FFFFFF;
            }
            p{
                font-family: 'Montserrat';
                font-style: normal;
                font-weight: 300;
                font-size: 20px;
                line-height: 24px;
                color: #FFFFFF;
                margin-top: 10px;
                margin-bottom: 22px;
            }
            .buttons-wrapper{
                display: flex;
                justify-content:space-between;
            }
            .btn{
                background: #D9D9D9;
                border-radius: 30px;
                width: 115px;
                font-family: 'Montserrat';
                font-style: normal;
                font-weight: 700;
                font-size: 20px;
                line-height: 24px;
                text-align: center;
                cursor: pointer;
                color: #1B1B1B;
            }
            .yellow{
                background: #FFC131;
                border-radius: 20px;
            }
        }
    }
    
}
